<template>
  <div>
    <a
      v-for="job in jobs"
      :key="job.id"
      :href="job.href"
      :ref="`job_${job.id}`"
      target="_blank"
      class="button button-outer job"
      @click="sendMetrika(job)"
    >
    <span v-for="(line, i) in title(job)" :key=i v-text=line></span>
    </a>
  </div>
</template>

<script>
/* eslint-disable no-irregular-whitespace */
export default {
  data: () => ({
    fetchUrl: '',
    linkFormat: '#',
    titleReplacers: {},
    jobs: [],
  }),

  computed: {
    replaceInTitle() {
      const replacements = Object.keys(this.titleReplacers)
      return title =>
        replacements
          .reduce(
            (t, key) => t.replace(new RegExp(key, 'g'), this.titleReplacers[key]),
            title
            );
    },
    title() {
      return ({ title, location, remote }) => {
        let result = [
          title,
          `-Â ${location.city}`
        ];
        if (remote) result[1] += '/remote';
        return result.map(t => this.replaceInTitle(t));
      }
    }
  },

  methods: {
    sendMetrika({ title, location }) {
      window.ym.send('vacancy_click', null, {
        title,
        city: location.city,
        });
    },

    hrefFor(job) {
      return this.linkFormat.replace(/(\{([\w\d_-]+)\})/ig, (fullMatch, replacement, field) => job[field])
    },

    async requestJobs() {
      const makeRequest = async ({ token = null } = {}) => {
        const tokenObj = token ? { token } : {};
        const response = await fetch(this.fetchUrl, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            query: '',
            ...tokenObj,
            location: [],
            department: ["IT"],
            worktype: [],
            remote: [],
          })
        });
        const { results, total, nextPage } = await response.json();
        results.forEach(job => {
          job.href = this.hrefFor(job);
          job.remote = Math.random() > .7;
          this.jobs.push(job)
        });
        if (total && nextPage && this.jobs.length < total)
          return makeRequest({ token: nextPage });
      };
      await makeRequest();
    },
  },

  async mounted() {
    const { fetchUrl, linkFormat, replace } = await window.getLoadedJSON('jobs');
    this.fetchUrl = fetchUrl;
    this.linkFormat = linkFormat;
    this.$set(this, 'titleReplacers', replace);
    await this.requestJobs();
    // eslint-disable-next-line no-console
    console.info({ jobs: JSON.parse(JSON.stringify(this.jobs)) })
    const showFn = el => () => el.classList.add('job-show')
    this.$nextTick(() =>
      Object.values(this.$refs).forEach(([ item ], i) =>
        // eslint-disable-next-line no-console
        setTimeout(showFn(item), 50 * i)
      )
    );
  }
};
</script>

<style lang="scss">
  .job {
    display: none;

    &-show {
      display: block;
    }

    > span {
      display: block;
    }
  }
</style>